.blog {
	padding: 520px 0 0 0;
	background: $bg-main;
	&__suptitle {
		background: $bg-accent;
		margin: 0 0 15px 0;
	}

	&__header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin: 0 0 80px 0;
	}

	&__title {
		color: $color-main;
	}

	&__btn {
		background: $bg-white;
		border: 1px solid $bg-white;
	}
	&__list {
		display: flex;
		gap: 32px;
	}
}
